:root {
  /* --main-color: #f2f2f2; */
  --main-color: rgb(0,0,0,.1)
}

body {
  background-color: var(--main-color);
}

/* reset css */
html {
  box-sizing: border-box;
  font-size: 16px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
/* reset css */
